.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

body {
  background-color: #fdf7ef;
  /* Adds a light grey background color to the entire app */
}



.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.file-selector {
  width: 100%;
  max-width: 600px;
  margin: auto;
}

.button {
  background-color: #fc4d1e;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
  font-family: "Arial";
  border-radius: 5px;
}

.button:hover {
  background-color: #e03e1a;
}

.button:active {
  background-color: #ff0000;
  color: white;
}

.image{
  position: absolute;
  height: auto;
  top: 65px;
  max-width: 25%;
  left: 0;
}