
.markdown-container {
    text-align: justify;
    margin: auto;
    margin-left:30%;

  }
  
  .markdown-container h1,
  .markdown-container h2,
  .markdown-container h3 {
    color: #000000;
    margin-top: 1px;
  }
  
  .markdown-container p {
    line-height: 1.6;
  }

  .markdown-container table, th, td {
    border: 1px solid;
    border-collapse: collapse;
    padding: 10px;
  }
